.container_gallery {
    display: flex;
    width: 100%;
}

.container_gallery .gallery {
    width: 100%;
    display: grid;
    grid-auto-flow: dense;
    grid-template:  "1fr 1fr 1fr 1fr 1fr" 33.33vh
                    "1fr 1fr 1fr 1fr 1fr" 33.33vh
                    "1fr 1fr 1fr 1fr 1fr" 33.33vh;
    // grid-gap: 1px;
}

.container_gallery .gallery .box {
    position: relative;
    background: #ccc;
}

.container_gallery .gallery .box img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.container_gallery .gallery .box.a,
.container_gallery .gallery .box.f {
    grid-row: span 2;
}

.container_gallery .gallery .box.b,
// .container_gallery .gallery .box.h,
.container_gallery .gallery .box.i,
.container_gallery .gallery .box.j {
    grid-column: span 2;
}

@media (max-width: 991px) {
    .container_gallery .gallery {
        grid-template:  "1fr 1fr 1fr" 250px
                        "1fr 1fr 1fr" 250px
                        "1fr 1fr 1fr" 250px
                        "1fr 1fr 1fr" 250px
                        "1fr 1fr 1fr" 250px;
    }
    .container_gallery .gallery .box.j {
        grid-column: span 1;
        grid-row: span 2;
    }
}

@media (max-width: 768px) {
    .container_gallery .gallery {
        grid-template:  "1fr 1fr" 200px
                        "1fr 1fr" 200px
                        "1fr 1fr" 200px
                        "1fr 1fr" 200px
                        "1fr 1fr" 200px
                        "1fr 1fr" 200px
                        "1fr 1fr" 200px;
    }
    .container_gallery .gallery .box.j {
        grid-row: span 1;
    }
}
