body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{background-color:var(--nav-bg);position:fixed;width:100%;box-sizing:border-box;box-shadow:var(--nav-shadow);transition:background-color 0.4s ease-out;z-index:5}nav ul li{display:inline;padding:10px}.nav-links{color:white;text-decoration:none}

.container{display:block;text-align:left;padding-left:5%;padding-right:5%}.container h6{font-size:20px}.container .custom_heading-container h2{margin-top:-10px;margin-bottom:-30px;font-size:35px}.container a{display:inline-block;padding:8px 25px;background-color:#000000;border:1px solid #000000;color:#ffffff;border-radius:25px;margin-top:10px;text-decoration:none}.container a:hover{background-color:transparent;color:#000000}.container p{margin-top:45px;line-height:1.5;font-weight:400;font-size:15px}.container .img-box img{width:100%}.container h6{text-transform:uppercase;color:#bdbbbb;font-weight:bold;margin:1px}

.container_gallery{display:flex;width:100%}.container_gallery .gallery{width:100%;display:grid;grid-auto-flow:dense;grid-template:"1fr 1fr 1fr 1fr 1fr" 33.33vh "1fr 1fr 1fr 1fr 1fr" 33.33vh "1fr 1fr 1fr 1fr 1fr" 33.33vh}.container_gallery .gallery .box{position:relative;background:#ccc}.container_gallery .gallery .box img{position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover}.container_gallery .gallery .box.a,.container_gallery .gallery .box.f{grid-row:span 2}.container_gallery .gallery .box.b,.container_gallery .gallery .box.i,.container_gallery .gallery .box.j{grid-column:span 2}@media (max-width: 991px){.container_gallery .gallery{grid-template:"1fr 1fr 1fr" 250px "1fr 1fr 1fr" 250px "1fr 1fr 1fr" 250px "1fr 1fr 1fr" 250px "1fr 1fr 1fr" 250px}.container_gallery .gallery .box.j{grid-column:span 1;grid-row:span 2}}@media (max-width: 768px){.container_gallery .gallery{grid-template:"1fr 1fr" 200px "1fr 1fr" 200px "1fr 1fr" 200px "1fr 1fr" 200px "1fr 1fr" 200px "1fr 1fr" 200px "1fr 1fr" 200px}.container_gallery .gallery .box.j{grid-row:span 1}}

.form-location{display:grid;margin-top:20px;padding-top:20px;margin-bottom:20px;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));grid-gap:5%}.form-location .contact_items{display:grid;grid-gap:2rem}.contact_form-container input{border:none;outline:none;border-bottom:1px solid #000;width:90%;margin:15px 0;background-color:transparent}.contact_form-container input::-webkit-input-placeholder{color:#595959;font-size:14px}.contact_form-container input:-ms-input-placeholder{color:#595959;font-size:14px}.contact_form-container input::-ms-input-placeholder{color:#595959;font-size:14px}.contact_form-container input::placeholder{color:#595959;font-size:14px}.contact_form-container input.message_input{margin-top:80px}.contact_form-container button{border:none;background-color:#000;color:#fff;font-size:15px;padding:15px 55px;border-radius:30px;text-transform:uppercase;border:1px solid #000000;color:#ffffff;border-radius:25px}.contact_form-container button:hover{background-color:transparent;color:#000000}.contact_items{margin:0 auto;display:flex;justify-content:space-between}.contact_items .item{width:250px;display:flex;align-items:center;text-align:center}.contact_items .item .img-box{width:60px;height:60px;border-radius:100%;background-repeat:no-repeat;background-position:center}.contact_items .item .detail-box{color:#5e5e5e}.contact_items .item .detail-box p{margin:0}.contact_items{position:relative}.contact_items a{position:relative}.contact_items .item .img-box.box-1{background-image:url(data:image/png;base64,)}.contact_items .item .img-box.box-2{background-image:url(data:image/png;base64,)}.contact_items .item .img-box.box-3{background-image:url(data:image/png;base64,)}


.footer{display:flex;flex-flow:row wrap;padding:5px 30px 20px 30px;color:rgba(255,255,255,0.883);background-color:#1d1b1e}.footer>*{flex:1 1 100%}.footer_addr h2{margin-top:1.3em;font-size:15px;font-weight:400}.footer_nav .nav_title{font-weight:400;font-size:15px}.footer address{font-style:normal;color:#999}.footer ul{list-style:none;padding-left:0}.footer li{line-height:2em}.footer a{text-decoration:none}.footer_nav{display:flex;flex-flow:row wrap}.footer_nav>*{flex:1 1 50%;margin-right:1.25em}.footer_nav .nav_ul a{color:#999}.footer_nav .nav_ul-extra{-webkit-column-count:2;column-count:2;grid-column-gap:1.25em;-webkit-column-gap:1.25em;column-gap:1.25em}.footer_nav .legal{display:flex;flex-wrap:wrap;color:#999}@media screen and (min-width: 24.375em){.legal{margin-left:auto}}@media screen and (min-width: 40.375em){.footer_nav>*{flex:1 1}.nav_item-extra{flex-grow:2}.footer_addr{flex:1 1}.footer_nav{flex:2 1}}

.home_section img{width:100%;min-height:auto}.headline{display:block;position:relative;color:#dfdbdb;font-weight:bold;width:100%;bottom:35vw;font-size:3.8vw;background-color:rgba(95,95,95,0.4)}.flip{height:7vw;overflow:hidden}.flip>div>div{display:inline-block;color:#fff;padding:0vw 1vw;height:5.5vw;margin-bottom:3.5vw}.flip div:first-child{-webkit-animation:show 5s linear infinite;animation:show 5s linear infinite}.flip div div{background:#1fe089}.flip div:first-child div{background:#4ec7f3}.flip div:last-child div{background:#DC143C}@-webkit-keyframes show{0%{margin-top:-25vw}5%{margin-top:-18vw}33%{margin-top:-18vw}38%{margin-top:-9vw}66%{margin-top:-9vw}71%{margin-top:0vw}99.99%{margin-top:0vw}100%{margin-top:-25vw}}@keyframes show{0%{margin-top:-25vw}5%{margin-top:-18vw}33%{margin-top:-18vw}38%{margin-top:-9vw}66%{margin-top:-9vw}71%{margin-top:0vw}99.99%{margin-top:0vw}100%{margin-top:-25vw}}.about_section{padding-bottom:3%;margin-top:-15%}.work_section{margin-top:50px}.contact_section{margin-top:50px}.map-box{margin-top:10%}.contact_header{font-size:25px;margin-bottom:-30px}

.header_section img{width:100%;min-height:auto}.content_section{margin-top:20px;margin-bottom:50px}

.content_contact{margin-bottom:50px}

.notfound{position:relative;height:100vh;box-sizing:border-box}.notfound .notfound-bg{position:absolute;width:100%;height:100%;background-image:url(/photography/static/media/bg3.c0070d2d.jpg);background-size:cover}.notfound .notfound-bg:after{content:'';position:absolute;width:100%;height:100%;left:0%;top:0%;background-color:rgba(0,0,0,0.25)}.notfound .notfound-content{position:absolute;left:50%;top:50%;max-width:600px;width:100%;text-align:center;padding:30px;line-height:1.4;transform:translate(-50%, -50%)}.notfound .notfound-content:after{content:'';position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);width:100%;height:600px;background-color:rgba(255,255,255,0.6);box-shadow:0px 0px 0px 30px rgba(255,255,255,0.7) inset;z-index:-1}.notfound-content .notfound-404{position:relative;height:200px}.notfound-content .notfound-404 h1{font-family:'Passion One', cursive;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);font-size:220px;margin:0px;color:#222225;text-transform:uppercase;text-align:center}.notfound-content h2{font-family:'Muli', sans-serif;font-size:26px;font-weight:400;text-transform:uppercase;color:#222225;margin-top:26px;margin-bottom:20px}.notfound-content a{font-family:'Muli', sans-serif;display:inline-block;font-weight:400;text-decoration:none;background-color:transparent;color:#222225;text-transform:uppercase;font-size:14px}@media only screen and (max-width: 480px){.notfound-content .notfound-404{height:146px;font-size:146px}.notfound-content h2{font-size:22px}}

.work_header img{width:100%;min-height:auto}

