.container {
    display: block;
    text-align: left;
    padding-left: 5%;
    padding-right: 5%;
}

.container h6 {
    font-size: 20px;
}

.container  .custom_heading-container h2{
    margin-top: -10px;
    margin-bottom: -30px;
    font-size: 35px;
}
  
.container a {
    display: inline-block;
    padding: 8px 25px;
    background-color: #000000;
    border: 1px solid #000000;
    color: #ffffff;
    border-radius: 25px;
    margin-top: 10px;
    text-decoration: none;
}

.container a:hover {
    background-color: transparent;
    color: #000000;
}
  
.container p {
    margin-top: 45px;
    line-height: 1.5;
    font-weight: 400;
    font-size: 15px;
}

.container .img-box img {
    width: 100%;
}
  
.container h6 {
    text-transform: uppercase;
    color: #bdbbbb;
    font-weight: bold;
    margin: 1px;
}
