
.form-location {
    display: grid;
    margin-top: 20px;
    padding-top: 20px;
    margin-bottom: 20px;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 5%
}

.form-location .contact_items {
    display: grid;
    grid-gap: 2rem;
}
 
.contact_form-container input {
    border: none;
    outline: none;
    border-bottom: 1px solid #000;
    width: 90%;
    margin: 15px 0;
    background-color: transparent;
}
  
.contact_form-container input::-webkit-input-placeholder {
    color: #595959;
    font-size: 14px;
}
  
.contact_form-container input:-ms-input-placeholder {
    color: #595959;
    font-size: 14px;
}
  
.contact_form-container input::-ms-input-placeholder {
    color: #595959;
    font-size: 14px;
}
  
.contact_form-container input::placeholder {
    color: #595959;
    font-size: 14px;
}
  
.contact_form-container input.message_input {
    margin-top: 80px;
}
  
.contact_form-container button {
    border: none;
    background-color: #000;
    color: #fff;
    font-size: 15px;
    padding: 15px 55px;
    border-radius: 30px;
    text-transform: uppercase;
    border: 1px solid #000000;
    color: #ffffff;
    border-radius: 25px;
}
  
.contact_form-container button:hover {
    background-color: transparent;
    color: #000000;
}

.contact_items {
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
  
.contact_items .item {
    width: 250px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
}
  
.contact_items .item .img-box {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    background-repeat: no-repeat;
    background-position: center;
}
  
.contact_items .item .detail-box {
    color: #5e5e5e;
}
  
.contact_items .item .detail-box p {
    margin: 0;
}
  
.contact_items {
    position: relative;
}
  
.contact_items a {
    position: relative;
}
  
.contact_items .item .img-box.box-1 {
    background-image: url(../images/location.png);
}
  
.contact_items .item .img-box.box-2 {
    background-image: url(../images/telephone.png);
}
  
.contact_items .item .img-box.box-3 {
    background-image: url(../images/envelope.png);
}
